import type { EventEmitter } from '@stencil/core';
import { Component, Element, Host, Method, Prop, State, Watch, h, Event } from '@stencil/core';

@Component({
	tag: 'o-select',
	styleUrl: 'o-select.scss',
	shadow: true,
	formAssociated: true
})
export class OSelect {
	private listbox: HTMLSlotElement;
	private popup: HTMLOPopupElement;
	private displayInput: HTMLInputElement;
	private valueInput: HTMLInputElement;
	private internals: ElementInternals | null = null;
	private defaultValue: string = '';
	private valueHasChanged: boolean = false;
	private currentOption: HTMLOOptionElement;

	@Element() host: HTMLOSelectElement;

	@Prop() label: string;

	@Prop() nameValue: string = 'default';

	@Prop({ mutable: true }) open: boolean = false;

	@Prop() placement: 'top' | 'bottom' = 'bottom';

	@Prop() placeholder: string = '';

	@Prop({ reflect: true }) disabled: boolean = false;

	@Prop() helpText: string;

	@Prop() required: boolean = false;

	@Prop() errorText: string = '';

	@Prop({ mutable: true, reflect: true }) value?: string = '';

	@State() isOpen: boolean = false;

	@State() isInitialized: boolean = false;

	@State() displayLabel = '';

	@State() hasFocus: boolean = false;

	@State() hasError: boolean = false;

	/** Emitted when the select value changes */
	@Event() oChange: EventEmitter<{ value: string }>;

	constructor() {
		// Validation to allow unit tests to run without ElementInternals support
		try {
			if (typeof this.host.attachInternals === 'function') {
				this.internals = this.host.attachInternals();
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('ElementInternals not supported', e.message);
		}
	}

	connectedCallback() {
		this.validateInput(this.value);
		this.updateFormValue(this.value);
		this.isInitialized = true;
	}

	componentDidLoad() {
		this.defaultValue = this.value;
	}

	@Watch('open')
	async handleOpenChange() {
		if (this.open && !this.disabled) {
			// reset current option
			const allOptions = this.getAllOptions();
			const selectedOption = allOptions?.filter((el) => el?.selected)[0];
			this.setCurrentOption(selectedOption || allOptions[0]);

			this.isOpen = true;
			this.addListeners();
			this.listbox.hidden = false;
			this.popup.active = true;

			requestAnimationFrame(() => {
				this.setCurrentOption(this.currentOption);
			});

			const { keyframes, options } = this.getAnimation('select.show');
			await Promise.resolve(this.animateTo(this.popup?.shadowRoot?.querySelector('div'), keyframes, options));
		} else {
			this.isOpen = false;
			this.removeListeners();
			this.popup.active = false;
			this.listbox.hidden = true;

			const { keyframes, options } = this.getAnimation('select.hide');
			await Promise.resolve(this.animateTo(this.popup?.shadowRoot?.querySelector('div'), keyframes, options));
		}
	}

	@Watch('disabled')
	handleDisabledChange() {
		if (this.disabled) {
			this.open = false;
			return;
		}
	}

	@Watch('value')
	async handleValueChange(newValue: string) {
		if (!this.valueHasChanged && this.defaultValue === newValue) {
			return;
		}

		const allOptions = this.getAllOptions();
		await this.setSelectedOption(allOptions?.filter((el) => this.value === el.value)[0]);

		this.validateInput(newValue);
		this.updateFormValue(newValue);
		this.oChange.emit({ value: this.value });
		this.valueHasChanged = true;
	}

	/* istanbul ignore next */
	@Watch('errorText')
	errorTextChanged() {
		this.validateInput(this.value);
		this.updateValidityState();
	}

	@Watch('helpText')
	helpTextChanged() {
		this.validateInput(this.value);
	}

	@Method()
	async show() {
		if (this.open || this.disabled) {
			return;
		}

		this.open = true;
	}

	@Method()
	async hide() {
		this.open = false;
	}

	handleFocus() {
		this.hasFocus = true;
	}

	handleBlur() {
		this.hasFocus = false;
	}

	handleDefaultSlotChange() {
		const allOptions = this.getAllOptions();
		const optionsValues: string[] = [];

		// Check for duplicate values
		allOptions.forEach((option) => optionsValues.push(option.value));

		// Select only the option that matches the new value
		if (this.value) this.setSelectedOption(allOptions?.filter((el) => this.value === el.value)[0]);
	}

	/* istanbul ignore next */
	addListeners() {
		document.addEventListener('mousedown', this.handleDocumentMouseDown);
		document.addEventListener('keydown', this.handleDocumentKeyDown);
	}

	/* istanbul ignore next */
	removeListeners() {
		document.removeEventListener('mousedown', this.handleDocumentMouseDown);
		document.removeEventListener('keydown', this.handleDocumentKeyDown);
	}

	handleDocumentKeyDown = (event: KeyboardEvent) => {
		if (event.key === 'Escape' && this.open) {
			event.preventDefault();
			event.stopPropagation();
			this.hide();
			this.displayInput.focus({ preventScroll: true });
		}

		if (event.key === 'Enter' || event.key === ' ') {
			event.preventDefault();
			event.stopImmediatePropagation();

			if (!this.open) {
				this.show();
				return;
			}

			if (this.currentOption && !this.currentOption.disabled) {
				this.valueHasChanged = true;
				this.setSelectedOption(this.currentOption);

				this.hide();
				this.displayInput.focus({ preventScroll: true });
			}

			return;
		}

		if (['ArrowUp', 'ArrowDown', 'Home', 'End'].includes(event.key)) {
			const allOptions = this.getAllOptions();

			const currentIndex = allOptions.indexOf(this.currentOption);
			let newIndex = Math.max(0, currentIndex);

			event.preventDefault();

			if (!this.open) {
				this.show();

				if (this.currentOption) {
					return;
				}
			}

			if (event.key === 'ArrowDown') {
				newIndex = currentIndex + 1;
				if (newIndex > allOptions.length - 1) newIndex = 0;
			} else if (event.key === 'ArrowUp') {
				newIndex = currentIndex - 1;
				if (newIndex < 0) newIndex = allOptions.length - 1;
			} else if (event.key === 'Home') {
				newIndex = 0;
			} else if (event.key === 'End') {
				newIndex = allOptions.length - 1;
			}

			this.setCurrentOption(allOptions[newIndex]);
		}
	};

	/* istanbul ignore next */
	handleDocumentMouseDown = (event: MouseEvent) => {
		const path = event.composedPath();
		if (this.host && !path.includes(this.host)) this.hide();
	};

	/* istanbul ignore next */
	handleComboboxMouseDown = (event: MouseEvent) => {
		if (this.disabled) {
			return;
		}

		event.preventDefault();
		this.displayInput.focus({ preventScroll: true });
		this.open = !this.open;
	};

	handleComboboxKeyDown = (event: KeyboardEvent) => {
		if (event.key === 'Tab') {
			return;
		}

		event.stopPropagation();
		this.handleDocumentKeyDown(event);
	};

	/* istanbul ignore next */
	handleOptionClick = (event: MouseEvent) => {
		const target = event.target as HTMLElement;
		const option = target.closest('o-option');

		if (option && !option.disabled && !(this.value === option?.value)) {
			this.valueHasChanged = true;
			this.setSelectedOption(option);
		}

		this.displayInput.focus({ preventScroll: true });
		this.hide();
	};

	getAllOptions() {
		return Array.from(this.host.querySelectorAll('o-option'));
	}

	async setSelectedOption(option: HTMLOOptionElement) {
		const allOptions = this.getAllOptions();
		allOptions.forEach((el) => (el.selected = false));

		if (option) option.selected = true;

		await this.updateSelection();
	}

	async updateSelection() {
		// ensure updateSelection does not count as update to dirty state
		const cachedValueHasChanged = this.valueHasChanged;

		const options = this.getAllOptions();
		const selectedOption = options.filter((el) => el.selected)[0];
		this.value = selectedOption?.value ?? '';
		this.displayLabel = (await selectedOption?.getTextContent?.()) ?? '';

		this.valueHasChanged = cachedValueHasChanged;
	}

	setCurrentOption(option: HTMLOOptionElement) {
		const allOptions = this.getAllOptions();

		allOptions.forEach((el) => {
			el.current = false;
			el.tabIndex = -1;
		});

		if (option) {
			this.currentOption = option;
			option.current = true;
			option.tabIndex = 0;
			option.focus();
		}
	}

	validateInput(value: string) {
		this.hasError = this.isInitialized && (!value || value.trim() === '') && this.errorText.trim() !== '';
		this.updateValidityState();
	}

	updateValidityState() {
		if (!this.internals) return;
		try {
			/* istanbul ignore if */
			if (this.hasError) {
				this.internals.setValidity?.({ customError: true }, this.errorText, this.valueInput);
			} else {
				this.internals.setValidity?.({});
			}
		} catch (e) {
			/* istanbul ignore next */
			console.warn('Validity state update not supported', e.message);
		}
	}

	updateFormValue(value: string) {
		/* istanbul ignore if */
		if (this.internals?.setFormValue) {
			try {
				this.internals.setFormValue(value || null);
			} catch (e) {
				console.warn('Form value update not supported', e.message);
			}
		}
	}

	getAnimation(animationName: string) {
		const animations = {
			'select.show': {
				keyframes: [
					{ opacity: 0, scale: 0.9 },
					{ opacity: 1, scale: 1 }
				],
				options: { duration: 100, easing: 'ease' }
			},
			'select.hide': {
				keyframes: [
					{ opacity: 1, scale: 1 },
					{ opacity: 0, scale: 0.9 }
				],
				options: { duration: 100, easing: 'ease' }
			}
		};

		return animations[animationName] || { keyframes: [], options: {} };
	}

	async animateTo(element: HTMLElement, keyframes: Keyframe[], options: KeyframeAnimationOptions) {
		if (element) {
			const animation = element.animate(keyframes, options);
			return new Promise<void>((resolve) => {
				animation.onfinish = () => resolve();
			});
		} else {
			return Promise.resolve();
		}
	}

	render() {
		const baseComboboxClasses =
			'body-default bg-background-default rounded-radius-lg border flex-grow flex w-full min-w-0 relative items-center justify-start align-middle overflow-hidden pe-3';

		const defaultComboboxClasses = !this.disabled ? `${baseComboboxClasses} cursor-pointer` : '';

		const disabledComboboxClasses = this.disabled
			? `${baseComboboxClasses} text-content-disabled bg-form-color-background-disabled cursor-not-allowed outline-none`
			: '';

		const baseFocusComboboxClasses = `${defaultComboboxClasses} outline outline-visible outline-width-lg outline-focus-ring-color-border-default outline-offset-0`;

		const focusComboboxClasses = this.hasFocus
			? this.hasError
				? `${baseFocusComboboxClasses} outline-border-status-error`
				: `${baseFocusComboboxClasses} outline-focus-ring-color-border-default`
			: '';

		const errorComboboxClasses = this.hasError
			? `${defaultComboboxClasses} border-border-status-error`
			: `${defaultComboboxClasses} border-border-default`;

		return (
			<Host>
				<div>
					{this.label && (
						<o-label
							htmlFor={this.nameValue}
							label={this.label}
							variant={this.hasError ? 'error' : this.disabled ? 'disabled' : undefined}
							required={this.required}
						></o-label>
					)}
					<o-popup
						ref={(el) => (this.popup = el)}
						placement={this.placement}
						flip
						distance={3}
						sync="width"
						class="inline-flex flex-auto w-full relative align-middle"
					>
						<div
							slot="anchor"
							class={`${defaultComboboxClasses} ${disabledComboboxClasses} ${focusComboboxClasses} ${errorComboboxClasses}`}
							onMouseDown={this.handleComboboxMouseDown}
							onKeyDown={this.handleComboboxKeyDown}
						>
							<input
								ref={(el) => (this.displayInput = el)}
								class={`relative w-full body-default placeholder:text-content-subtle placeholder:ps-2 text-content-default border-transparent focus:border-transparent focus:ring-0 rounded-radius-lg overflow-hidden py-2 m-0 focus:outline-none ${
									this.disabled
										? 'bg-form-color-background-disabled text-content-disabled cursor-not-allowed'
										: 'cursor-pointer'
								}`}
								type="text"
								placeholder={this.placeholder}
								value={this.displayLabel}
								disabled={this.disabled}
								readonly
								aria-expanded={this.open ? 'true' : 'false'}
								aria-haspopup="listbox"
								aria-labelledby="label"
								aria-disabled={this.disabled ? 'true' : 'false'}
								role="combobox"
								tabIndex={0}
								onFocus={() => this.handleFocus()}
								onBlur={() => this.handleBlur()}
							></input>
							<input
								ref={(el) => (this.valueInput = el)}
								class={`opacity-0 absolute size-full body-default border-none rounded-radius-lg p-0 m-0 -z-10`}
								type="text"
								disabled={this.disabled}
								value={this.value}
								tabIndex={-1}
								aria-hidden="true"
							></input>
							<o-icon
								class={`${
									this.isOpen ? 'rotate-180' : 'rotate-0'
								} flex-none flex items-center ms-2 transition-[rotate] duration-[250ms]`}
								name="chevron-down"
								color={this.disabled ? 'content-disabled' : 'content-default'}
							></o-icon>
						</div>
						<div
							class="relative border border-border-default bg-background-default rounded-radius-lg overflow-hidden"
							role="listbox"
							tabIndex={-1}
							onMouseUp={this.handleOptionClick}
						>
							<div class="overflow-auto max-h-40">
								<slot
									ref={(el) => (this.listbox = el as HTMLSlotElement)}
									onSlotchange={() => this.handleDefaultSlotChange()}
								></slot>
							</div>
						</div>
					</o-popup>
					{this.helpText && (
						<div class={`body-default mt-1 ${this.disabled ? 'text-content-disabled' : 'text-content-default'}`}>
							{this.helpText}
						</div>
					)}
					{this.hasError && (
						<div class="body-default mt-1">
							<span class="text-content-status-error">{this.errorText}</span>
						</div>
					)}
				</div>
			</Host>
		);
	}
}
